import { stringify } from '@angular/compiler/src/util';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { NewRequest } from 'src/app/newRequest.model';
import { Order } from 'src/app/Order.model';
import { Product } from 'src/app/product.model';
import { ProductserviceService } from 'src/app/productservice.service';
import { Quotation } from 'src/app/Quotation.model';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-cviewquotation',
  templateUrl: './cviewquotation.component.html',
  styleUrls: ['./cviewquotation.component.css']
})
export class CviewquotationComponent implements OnInit {

  customer:User=JSON.parse(sessionStorage["user_dtls"]);
  @Input("quotations") allQuotations:[];
  @Input("request")request:NewRequest;
  @Output("selectedQuotation") selectedQuotation=new EventEmitter();
  createdProduct:Product;
  placedOrder:Order;

  constructor(private prodService:ProductserviceService, private route :ActivatedRoute, private router: Router) {}

  ngOnInit(): void {
  }


  getClass(quotation:Quotation):String{

    if(quotation.AcceptanceStatus==0){
      return "badge badge-warning";
    }
    else if(quotation.AcceptanceStatus==1){
      return "badge badge-success";
    }
    else if(quotation.AcceptanceStatus==2){
      return "badge badge-danger";
    }
  }

  getStatus(quotation:Quotation):String{
    
    if(quotation.AcceptanceStatus==0){
      return "New";
    }
    else if(quotation.AcceptanceStatus==1){
      return "Accepted";
    }
    else if(quotation.AcceptanceStatus==2){
      return "Rejected";
    }
  }

  viewQuotation(quotation:Quotation){
    this.selectedQuotation.emit(quotation);
  }

  acceptQuotation(quotation:Quotation){
    this.prodService.acceptQuotation(quotation).subscribe(data=>{
      this.request.IsValid=false;
      alert("Quotation Accepted and others rejected...");

      this.createdProduct=new Product(0, quotation.ServiceProviderId, this.request.Category, this.request.Dimension, 
        this.request.Weight, this.request.Controller, this.request.Sensor, this.request.BluetoothEnabled, 
        this.request.InternetControlled, this.request.PhysicalRemote, this.request.CameraEquipped, 
        this.request.MotorType, this.request.PowerSupplyUnit, this.request.WiringType, this.request.BuildType, null, null, 
        quotation.FinalPrice, null, 0);

      this.prodService.addProduct(this.createdProduct).subscribe(
        data=>{
          
          alert("product submitted....");

          this.createdProduct=data;

          this.placedOrder=new Order(0, this.customer, this.createdProduct, quotation.ServiceProviderId, 
            1, quotation.FinalPrice, 0, false, new Date(), null);
  
          this.prodService.placeOrder(this.placedOrder).subscribe(
            data=>{
              console.log(data);
              alert("Order placed successfully....");
            }, err=>{
              console.log(err);
              alert("Order could not be placed....!!!");
            }
          )
        }, err=>{
          console.log(err);
          alert("Product NOT added...!!!");
        }
      )

      this.router.navigate(["customer/allrequests"]);

      }, 
      err=>{
        console.log(err);
        alert("Quotation NOT Accepted...!!!")
      });
  }

  rejectQuotation(quotation:Quotation){
    this.prodService.rejectQuotation(quotation).subscribe(data=>{
      alert("Quotation Rejected..."); 
      this.router.navigate(["/customer/allrequests"]);
    },
      err=>{console.log(err);
      alert("Quotation NOT Rejected...!!!")
    });
  }

}
