import { Route } from '@angular/compiler/src/core';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MembershipService } from 'src/app/membership/membership.service';

@Component({
  selector: 'app-adminmenubar',
  templateUrl: './adminmenubar.component.html',
  styleUrls: ['./adminmenubar.component.css']
})
export class AdminmenubarComponent implements OnInit {

  constructor(private ms:MembershipService, private router:Router) { }

  ngOnInit(): void {
  }

  showAllUsers(){
    this.router.navigate(["admin/users"]);
  }

  showAllServiceProviders(){
    this.router.navigate(["admin/serviceprovider"]);
  }

  goToLogout(){
    this.router.navigate(["logout"]);
  }

}
