import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Product } from 'src/app/product.model';
import { ProductserviceService } from 'src/app/productservice.service';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-cproductlist',
  templateUrl: './cproductlist.component.html',
  styleUrls: ['./cproductlist.component.scss']
})
export class CproductlistComponent implements OnInit {

  products:[];
  selectedProduct:Product;
  customer:User=JSON.parse(sessionStorage["user_dtls"]);
  isProductViewSelected:boolean=false;
  
  

  constructor(private prodService:ProductserviceService, private route :ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    this.prodService.getProductsByCategory('WINDOW').subscribe(
      data=>{
        this.products=data;
      },
      err=>{
        console.log(err);
      }
    )
  }


  getProductsByCategory(category:string){
    this.prodService.getProductsByCategory(category).subscribe(data=>this.products=data, err=>console.log(err));
  }

  viewProductClicked(product:Product){
    this.isProductViewSelected=true;
    this.selectedProduct=product;
  }

  showAllRequests(){
    this.router.navigate(["./allrequests"], {relativeTo:this.route});
  }

  showAllOrders(){
    this.router.navigate(["./orders"], {relativeTo:this.route});
  }

}
