import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import { Observable } from 'rxjs';

import { User } from '../User.model';

@Injectable({
  providedIn: 'root'
})
export class MembershipService {

  loginStatus:boolean=false;
  user:User;

 // baseUrl:string="http://localhost:8282/users/";

  baseUrlLogin="http://localhost:64734/login";
  baseUrlUserRole="http://localhost:64734/userrole/";
  baseUrlUsers="http://localhost:64734/users/"

  constructor(private http:HttpClient) { }

  validateUser(mail:String , pass:String):Observable<any>{
    let body = {
      email: mail, 
      password: pass
    };
    
    return this.http.post(this.baseUrlLogin, body);
  }

  getUsersByRole(role:String):Observable<any>{
    let roleId:number;
    switch(role){
      case "ADMIN": roleId=0;
      break;
      case "CUSTOMER": roleId=1;
      break;
      case "SERVICE": roleId=2;
      break;
    }
    return this.http.get(this.baseUrlUserRole + roleId);
  }

  removeUser(customer:User):Observable<any>{
    return this.http.delete(this.baseUrlUsers + customer.UserId);
  }

  registerUser(user:User):Observable<any>{
    return this.http.post(this.baseUrlUsers, user);
  }



}
