import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Order } from 'src/app/Order.model';
import { ProductserviceService } from 'src/app/productservice.service';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-corders',
  templateUrl: './corders.component.html',
  styleUrls: ['./corders.component.css']
})
export class CordersComponent implements OnInit {

  allOrders:[];
  customer:User=JSON.parse(sessionStorage["user_dtls"]);

  constructor(private prodService:ProductserviceService, private route :ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    this.prodService.getOrders(this.customer.UserId).subscribe(
      data=>{
        this.allOrders=data;
      }, err=>{
        console.log(err);
      }
    )
  }

  getStatus(order:Order){
    if(order.OrderStatus==0){
      return "NEW";
    }
    else if(order.OrderStatus==2){
      return "UNDERPROCESSING";
    }
    else if(order.OrderStatus==1){
      return "REJECTED";
    }
    else if(order.OrderStatus==3){
      return "COMPLETED";
    }
  }

  getClass(order:Order){
    if(order.OrderStatus==0){
      return "badge badge-warning";
    }
    else if(order.OrderStatus==2){
      return "badge badge-info";
      
    }
    else if(order.OrderStatus==1){
      return "badge badge-danger";
    }
    else if(order.OrderStatus==3){
      return "badge badge-success";
    }
  }

  payNow(order:Order){
    this.router.navigate(["payment/"+order.OrderId]);
  }

}
