import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductserviceService } from 'src/app/productservice.service';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-servedproducts',
  templateUrl: './servedproducts.component.html',
  styleUrls: ['./servedproducts.component.scss']
})
export class ServedproductsComponent implements OnInit {

  serviceProvider:User=JSON.parse(sessionStorage["user_dtls"]);
  servedProductList:[];

  constructor(private prodService:ProductserviceService, private route :ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    this.prodService.getservedProductsforServiceProvider(this.serviceProvider.UserId).subscribe(
      data=>{
        this.servedProductList=data;
      },
      err=>{
        console.log(err);
      }
    )
  }




  viewNewRequests(){
    this.router.navigate(["serviceprovider/newRequests"]);
  }

}
