import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MembershipService } from 'src/app/membership/membership.service';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-addserviceprovider',
  templateUrl: './addserviceprovider.component.html',
  styleUrls: ['./addserviceprovider.component.css']
})
export class AddserviceproviderComponent implements OnInit {

  constructor(private ms : MembershipService, private router:Router) { }

  ngOnInit(): void {
  }

  user:User=new User(null, null, null, null, null, 2, null, null, null);

  addServiceProvider(){
    this.ms.registerUser(this.user).subscribe(
      data=>{
        if(data==null){
          alert("Email already exists...!!!");
        }
        else{
        alert("Service Provider added successfully...");
        this.router.navigate(["admin/serviceprovider"]);
        }
      }
    )
  }

}
