import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Order } from 'src/app/Order.model';
import { Payment } from 'src/app/Payment.model';
import { ProductserviceService } from 'src/app/productservice.service';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.css']
})
export class PaymentComponent implements OnInit {

  orderId:number;
  order:Order;
  newPayment:Payment;

  constructor(private route:ActivatedRoute, private prodService:ProductserviceService, private router:Router) { }

  ngOnInit(): void {

    this.route.paramMap.subscribe(params=>{
      this.orderId=Number(params.get("id"));
      this.prodService.getOrderById(this.orderId).subscribe(data=>{
        this.order=data; 
        this.newPayment=new Payment(null, this.order, new Date(), this.order.TotalAmount);
      }, err=>console.log(err));
    });

  }

  makePayment(){
    this.prodService.insertPayment(this.newPayment).subscribe(
      data=>{
        alert("Payment successful...");
        this.router.navigate(["customer/orders"]);
      }, err=>{
        console.log(err);
        alert("Payment failed...!!");
      }
    )
  }

}
