import { Component, OnInit } from '@angular/core';
import { ProductserviceService } from 'src/app/productservice.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NewRequest } from 'src/app/newRequest.model';
import { Quotation } from 'src/app/Quotation.model';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-screatequotation',
  templateUrl: './screatequotation.component.html',
  styleUrls: ['./screatequotation.component.css']
})
export class ScreatequotationComponent implements OnInit {

  requestId:number;
  request:NewRequest;
  newQuotation:Quotation;
  serviceProvider:User=JSON.parse(sessionStorage["user_dtls"]);

  constructor(private prodService:ProductserviceService, private route :ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe(params=>{
      this.requestId=Number(params.get("id"));
      
      this.prodService.getRequestById(this.requestId).subscribe(data=>{
        this.request=data; 
        alert("request loaded");

        this.newQuotation=new Quotation(0, this.request, this.serviceProvider, null, null, 0);

      }, err=>console.log(err));
    });
  }


  submitNewQuotation(){
    alert("submitting...");

    this.prodService.submitNewQuotation(this.newQuotation).subscribe(data=>{
      alert("Quotation Submitted...");
      this.router.navigate(["serviceprovider/newRequests"]);
    }, err=>{
      console.log(err);
      alert("Quotation NOT Submitted...");
    })
  }


}
