import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MembershipService } from 'src/app/membership/membership.service';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-auserslist',
  templateUrl: './auserslist.component.html',
  styleUrls: ['./auserslist.component.css']
})
export class AuserslistComponent implements OnInit {

  admin:User=JSON.parse(sessionStorage["user_dtls"]);
  usersList:[];

  constructor(private ms:MembershipService, private router:Router) { }

  ngOnInit(): void {
    this.ms.getUsersByRole("CUSTOMER").subscribe(
      data=>{
        this.usersList=data;
      }, err=>{
        console.log(err);
      }
    );
  }

  removeUser(customer:User){
    this.ms.removeUser(customer).subscribe(
      data=>{
        console.log(data);
        alert("Customer Removed...");
        this.router.navigate(["admin"]);
      },
      err=>{console.log(err);
        alert("Customer NOT removed...!!!");
      }
    )
  }

}
