import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { StandardModule } from '../standard/standard.module';
import { MembershipModule } from '../membership/membership.module';
import { HomeComponent } from '../standard/home/home.component';
import { AboutusComponent } from '../standard/aboutus/aboutus.component';
import { ContactusComponent } from '../standard/contactus/contactus.component';
import { ContainerComponent } from './container/container.component';
import { LoginComponent } from '../membership/login/login.component';
import { CproductlistComponent } from '../customer/cproductlist/cproductlist.component';
import { CproductdetailsComponent } from '../customer/cproductdetails/cproductdetails.component';
import { CustomerModule } from '../customer/customer.module';
import { CnewrequestComponent } from '../customer/cnewrequest/cnewrequest.component';
import { CallrequestsComponent } from '../customer/callrequests/callrequests.component';
import { CordersComponent } from '../customer/corders/corders.component';
import { ServiceproviderModule } from '../serviceprovider/serviceprovider.module';
import { ServicehomeComponent } from '../serviceprovider/servicehome/servicehome.component';
import { NewrequestsComponent } from '../serviceprovider/newrequests/newrequests.component';
import { ScreatequotationComponent } from '../serviceprovider/screatequotation/screatequotation.component';
import { SordersComponent } from '../serviceprovider/sorders/sorders.component';
import { SquotationssentComponent } from '../serviceprovider/squotationssent/squotationssent.component';
import { LogoutComponent } from '../membership/logout/logout.component';
import { AdminhomeComponent } from '../admin/adminhome/adminhome.component';
import { AdminModule } from '../admin/admin.module';
import { AuserslistComponent } from '../admin/auserslist/auserslist.component';
import { AserviceproviderlistComponent } from '../admin/aserviceproviderlist/aserviceproviderlist.component';
import { RegisterComponent } from '../membership/register/register.component';
import { PaymentComponent } from '../customer/payment/payment.component';
import { AddserviceproviderComponent } from '../admin/addserviceprovider/addserviceprovider.component';


const routes:Routes = [
  {path:"", redirectTo:"home", pathMatch:"full"},
  {path:"home", component:HomeComponent},
  {path:"aboutus", component:AboutusComponent},
  {path:"contactus", component:ContactusComponent},
  {path:"login", component:LoginComponent},
  {path:"register", component:RegisterComponent},
  {path:"admin", component:AdminhomeComponent},
  {path:"customer", component:CproductlistComponent},
  {path:"details/:id", component:CproductdetailsComponent},
  {path:"customize", component:CnewrequestComponent},
  {path:"customer/allrequests", component:CallrequestsComponent},
  {path:"customer/orders", component:CordersComponent},
  {path:"serviceprovider", component:ServicehomeComponent},
  {path:"serviceprovider/newRequests", component:NewrequestsComponent},
  {path:"serviceprovider/newRequests/quotation/:id", component:ScreatequotationComponent},
  {path:"serviceprovider/orders", component:SordersComponent},
  {path:"serviceprovider/quotations", component:SquotationssentComponent},
  {path:"admin/users", component:AuserslistComponent},
  {path:"admin/serviceprovider", component:AserviceproviderlistComponent},
  {path:"logout", component:LogoutComponent},
  {path:"payment/:id", component:PaymentComponent},
  {path:"admin/serviceprovider/add", component:AddserviceproviderComponent},
  
];


@NgModule({
  declarations: [ContainerComponent],
  imports: [
    CommonModule, StandardModule, MembershipModule, RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload' }), CustomerModule, ServiceproviderModule, AdminModule
  ],
  exports:[ContainerComponent]
})
export class SpaModule { }
