import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbDateAdapter } from '@ng-bootstrap/ng-bootstrap';
import { Order } from 'src/app/Order.model';
import { ProductserviceService } from 'src/app/productservice.service';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-sorders',
  templateUrl: './sorders.component.html',
  styleUrls: ['./sorders.component.css']
})
export class SordersComponent implements OnInit {

  ordersList:[];
  serviceProvider:User=JSON.parse(sessionStorage["user_dtls"]);

  constructor(private prodService:ProductserviceService, private route :ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    this.prodService.getOrdersByServiceProviderId(this.serviceProvider.UserId).subscribe(data=>{
      this.ordersList=data;
    }, err=>{
      console.log(err);
    });
  }

  getClass(order:Order){
    if(order.OrderStatus==0){
      return "badge badge-warning";
    }
    else if(order.OrderStatus==2){
      return "badge badge-info";
    }
    else if(order.OrderStatus==3){
      return "badge badge-success";
    }
  }

  getStatus(order:Order){
    if(order.OrderStatus==0){
      return "NEW";
    }
    else if(order.OrderStatus==2){
      return "UNDERPROCESSING";
    }
    else if(order.OrderStatus==3){
      return "COMPLETED";
    }
  }

  getPaymentClass(order:Order){
    if(order.PaymentStatus==false){
      return "badge badge-danger";
    }
    else{
      return "badge badge-success";
    }
  }

  getPaymentStatus(order:Order){
    if(order.PaymentStatus==false){
      return "PENDING";
    }
    else{
      return "DONE";
    }
  }

  changeOrderStatus(event:any, order:Order){
    order.OrderStatus=event.target.value;
    this.prodService.placeOrder(order).subscribe(data=>alert("Status Changed..."), err=>alert("Status not changed...!!!"));
  }

}
