import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductserviceService } from 'src/app/productservice.service';
import { Quotation } from 'src/app/Quotation.model';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-squotationssent',
  templateUrl: './squotationssent.component.html',
  styleUrls: ['./squotationssent.component.css']
})
export class SquotationssentComponent implements OnInit {

  serviceProvider:User=JSON.parse(sessionStorage["user_dtls"]);
  allQuotationssent:[]=null;
  newQuotationsPendingFromUser = new Array();
  oldQuotations = new Array();

  constructor(private prodService:ProductserviceService, private route :ActivatedRoute, private router: Router) {  }

  ngOnInit(): void {
    this.prodService.getAllQuotationsSentForServiceProvider(this.serviceProvider.UserId).subscribe(data=>{
      this.allQuotationssent=data;

      this.allQuotationssent.forEach((quotation:Quotation)=>{
        if(quotation.AcceptanceStatus==0){
          this.newQuotationsPendingFromUser.push(quotation);
        }else{
          this.oldQuotations.push(quotation);
        }
      });

    }, err=>{
      console.log(err);
    });
  }

  getClass(quotation:Quotation){
    if(quotation.AcceptanceStatus==0){
      return "badge badge-warning";
    }
    else if(quotation.AcceptanceStatus==1){
      return "badge badge-success";
    }
    else if(quotation.AcceptanceStatus==2){
      return "badge badge-danger";
    }
  }

  getStatus(quotation:Quotation){
    if(quotation.AcceptanceStatus==0){
      return "New";
    }
    else if(quotation.AcceptanceStatus==1){
      return "Accepted";
    }
    else if(quotation.AcceptanceStatus==2){
      return "Rejected";
    }
  }

}
