import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MembershipService } from 'src/app/membership/membership.service';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-aserviceproviderlist',
  templateUrl: './aserviceproviderlist.component.html',
  styleUrls: ['./aserviceproviderlist.component.css']
})
export class AserviceproviderlistComponent implements OnInit {

  admin:User=JSON.parse(sessionStorage["user_dtls"]);
  serviceProvidersList:[];

  constructor(private ms:MembershipService, private router:Router, private route:ActivatedRoute) { }

  ngOnInit(): void {
    this.ms.getUsersByRole("SERVICE").subscribe(
      data=>{
        this.serviceProvidersList=data;
      }, err=>{
        console.log(err);
      }
    );
  }

  showAddUser(){
    this.router.navigate(['./add'], {relativeTo:this.route});
  }

  removeUser(customer:User){
    this.ms.removeUser(customer).subscribe(
      data=>{
        console.log(data);
        alert("Service Provider Removed...");
        this.router.navigate(["admin/serviceprovider"]); 
      },
      err=>{console.log(err);
        alert("Service Provider NOT removed...!!!");
      }
    )
  }

}
