import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { User } from 'src/app/User.model';
import { MembershipService } from '../membership.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  @Output("flag") flag=new EventEmitter();
  constructor(private ms : MembershipService, private router:Router) { }

  ngOnInit(): void {
  }

  user:User=new User(0, null, null, null, null, 1, null, null, null);
 msg='';
  
  registerUser()
  {
    this.ms.registerUser(this.user).subscribe(
      data =>
      {
        if(data==null){
          alert("Email already exists...!!!");
          this.router.navigate(["/register"]);
        }
        else{
        alert("Registration successful..");
        this.msg="Registration success";
        this.flag.emit(false);
        this.router.navigate(["/login"]);
        }
        
      },
      error =>{
        alert("Registration failed...!!!");
        this.msg="error";
      }
      );
  }

}
