import { Route } from '@angular/compiler/src/core';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NewRequest } from 'src/app/newRequest.model';
import { ProductserviceService } from 'src/app/productservice.service';

@Component({
  selector: 'app-srequestdetails',
  templateUrl: './srequestdetails.component.html',
  styleUrls: ['./srequestdetails.component.css']
})
export class SrequestdetailsComponent implements OnInit {

  @Input("request") request:NewRequest;
  @Output("cancelButton") cancelButton=new EventEmitter();

  constructor(private prodService:ProductserviceService, private route :ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
  }

  cancel(){
    this.cancelButton.emit(false);
    this.request=undefined;
  }

  createQuotation(){
    alert("Create Quotation");
    this.router.navigate(["./quotation/"+this.request.RequestId],{relativeTo:this.route});

  }

}
