import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NewRequest } from 'src/app/newRequest.model';
import { Order } from 'src/app/Order.model';
import { Product } from 'src/app/product.model';
import { ProductserviceService } from 'src/app/productservice.service';
import { ProductstateserviceService } from 'src/app/productstateservice.service';
import { User } from 'src/app/User.model';

@Component({
  selector: 'app-cproductdetails',
  templateUrl: './cproductdetails.component.html',
  styleUrls: ['./cproductdetails.component.css']
})
export class CproductdetailsComponent implements OnInit {

  @Input("product")product:Product=null;
  newRequestObj:NewRequest;
  customer:User=JSON.parse(sessionStorage["user_dtls"]);
  newOrder:Order;
  newProduct:Product;


  constructor(private prodService: ProductserviceService, private prodstate:ProductstateserviceService, private route :ActivatedRoute, private router: Router) { }

  ngOnInit(): void {}

  goToNewRequest(){
    this.prodstate.productState=this.product;
    this.prodstate.newRequestObj=new NewRequest(null, this.customer, null, this.product.Category, this.product.Dimension, this.product.Weight, this.product.Controller, 
      this.product.Sensor, this.product.BuildType, this.product.MotorType, this.product.WiringType, this.product.PowerSupplyUnit, 
      this.product.BluetoothEnabled, this.product.InternetControlled, this.product.CameraEquipped, this.product.PhysicalRemote, null, true);
    this.router.navigate(['./customize']);
  }

  goToNewOrder(){
    this.newProduct=this.product;
    this.newProduct.ProductId=null;
    this.prodService.addProduct(this.newProduct).subscribe(
      data=>{
        alert("product added....");
        this.newProduct=data;
        this.newOrder=new Order(null, this.customer, this.newProduct, this.newProduct.ServiceProviderId, 
          1, this.newProduct.FinalPrice, 0, false, new Date(), null);

        this.prodService.placeOrder(this.newOrder).subscribe(
          data=>{
            alert("Order Placed successfully...");
            this.router.navigate(["customer"]);
          }, err=>{
            console.log(err);
            alert("Order NOT placed...!!!");
          }
        )
      }, err=>{
        console.log(err);
        alert("Product NOT added...!!!");
      }
    )
  }
}

